import React, { Component, Fragment } from 'react';
import Page from 'components/LayoutComponents/Page';
import { Link } from 'react-router-dom';
import { ColEditDelete, TableData } from 'components/common';
import { BtnAddNew } from 'components/common/button';
import { Card, Row, Col, message } from 'antd';
import to from 'await-to-js';
import { getAllGradeNote, deleteGradeNote } from 'services/gradeNote';
import routeConstant from 'routes/route/routeConstant';

const ROUTER_FORM_DATA = routeConstant.NHAP_DIEM__GHI_CHU_NHAP_DIEM__NHAP_DU_LIEU;

class GradeNote extends Component {
  state = {
    // dataTable: [],
    loading: false,
    reload: 0,
  };

  componentDidMount() {}

  confirmDelete = async userId => {
    const [error] = await to(deleteGradeNote(userId));
    if (error) return;
    message.success('Xa thnh cng');
    this.setState({
      reload: this.state.reload + 1,
    });
  };

  cancel = () => {};

  renderTable = () => {
    const { reload } = this.state;
    const columns = [
      {
        title: 'Id',
        dataIndex: 'id',
        key: 'id',
      },
      {
        title: 'Ghi ch nhp im',
        dataIndex: 'label',
        key: 'label',
      },
      {
        title: 'Thao tc',
        dataIndex: 'operation',
        render: (text, record) => (
          <Fragment>
            <ColEditDelete
              linkEdit={{
                to: {
                  pathname: ROUTER_FORM_DATA,
                  search: `?id=${record.id}`,
                },
              }}
              onConfirmDelete={() => this.confirmDelete(record.id)}
            />
          </Fragment>
        ),
      },
    ];
    return (
      <TableData
        loading={this.state.loading}
        paramSearch={{}}
        service={getAllGradeNote}
        columns={columns}
        reload={reload}
        rowKey="id"
      />
    );
  };

  render() {
    return (
      <Card>
        <Row>
          <Col span={24}>
            <Link to={ROUTER_FORM_DATA}>
              <BtnAddNew />
            </Link>
          </Col>
        </Row>
        <Row>
          <Col span={24}>{this.renderTable()}</Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <GradeNote />
  </Page>
);



